'use client';
import { useState } from 'react';
import { CanvasTableCard } from '@/components/tables/CanvasTableCard';
import { AnimatedBackground } from '@/components/layout/AnimatedBackground';
import { MenuButton } from '@/components/layout/MenuButton';
import { FullscreenMenu } from '@/components/layout/FullscreenMenu';
import { StatsScreen } from '@/components/layout/StatsScreen';
import { ReferralTree } from '@/components/layout/ReferralTree';
import { ThemeToggle } from '@/components/layout/ThemeToggle';
import { RegistrationScreen } from '@/components/layout/RegistrationScreen';

const MOCK_USER = {
  nickname: 'crypto_king',
  totalEarned: 1247.5,
  partnersCount: 47,
  activeTables: 5,
  rank: 23
};

const TABLES = [
  { id: 1, price: 10, cycles: 3, slots: [1, 1, 1, 0], isActive: true },
  { id: 2, price: 20, cycles: 2, slots: [1, 1, 0, 0], isActive: true },
  { id: 3, price: 40, cycles: 1, slots: [1, 0, 0, 0], isActive: true },
  { id: 4, price: 80, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 5, price: 160, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 6, price: 320, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 7, price: 640, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 8, price: 1280, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 9, price: 2560, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 10, price: 5120, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 11, price: 10240, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
  { id: 12, price: 20480, cycles: 0, slots: [0, 0, 0, 0], isActive: false },
];

export default function TablesPage() {
  const [showRegistration, setShowRegistration] = useState(true);
  const [menuOpen, setMenuOpen] = useState(false);
  const [activeScreen, setActiveScreen] = useState<'tables' | 'stats' | 'tree'>('tables');

  if (showRegistration) {
    return <RegistrationScreen onComplete={() => setShowRegistration(false)} />;
  }

  const handleMenuSelect = (screen: 'tables' | 'stats' | 'tree') => {
    setActiveScreen(screen);
    setMenuOpen(false);
  };

  return (
    <div style={{
      minHeight: '100vh',
      background: 'linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%)',
      position: 'relative',
      overflow: 'hidden'
    }}>
      <AnimatedBackground />

      <div style={{
        position: 'fixed',
        top: '1.5rem',
        right: '1.5rem',
        zIndex: 40,
        display: 'flex',
        gap: '1rem'
      }}>
        <ThemeToggle />
        <MenuButton onClick={() => setMenuOpen(true)} />
      </div>

      <FullscreenMenu
        isOpen={menuOpen}
        onClose={() => setMenuOpen(false)}
        onNavigate={handleMenuSelect}
        currentScreen={activeScreen}
      />

      {activeScreen === 'tables' && (
        <div style={{
          position: 'relative',
          zIndex: 10,
          padding: '2rem 1rem',
          paddingTop: '5rem'
        }}>
          <div style={{
            maxWidth: '1200px',
            margin: '0 auto'
          }}>
            <div style={{
              textAlign: 'center',
              marginBottom: '3rem'
            }}>
              <h1 style={{
                fontSize: '2.5rem',
                fontWeight: 700,
                background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
                marginBottom: '0.5rem'
              }}>
                Matrix TON
              </h1>
              <p style={{
                color: 'rgba(255, 255, 255, 0.7)',
                fontSize: '1.1rem'
              }}>
                Welcome back, {MOCK_USER.nickname}!
              </p>
            </div>

            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))',
              gap: '1.5rem',
              marginBottom: '3rem'
            }}>
              {TABLES.map((table) => (
                <CanvasTableCard
                  key={table.id}
                  tableNumber={table.id}
                  price={table.price}
                  cycles={table.cycles}
                  slots={table.slots}
                  isActive={table.isActive}
                />
              ))}
            </div>
          </div>
        </div>
      )}

      {activeScreen === 'stats' && <StatsScreen user={MOCK_USER} />}
      {activeScreen === 'tree' && <ReferralTree />}

      <div style={{
        position: 'fixed',
        bottom: '2rem',
        left: '50%',
        transform: 'translateX(-50%)',
        zIndex: 50,
        animation: 'bounce 2s infinite'
      }}>
        <div 
          style={{
            width: '3rem',
            height: '3rem',
            borderRadius: '50%',
            background: 'rgba(102, 126, 234, 0.2)',
            backdropFilter: 'blur(10px)',
            border: '2px solid rgba(102, 126, 234, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer'
          }}
          onClick={() => window.scrollBy({ top: 300, behavior: 'smooth' })}
        >
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#667eea" strokeWidth="3">
            <polyline points="6 9 12 15 18 9"></polyline>
          </svg>
        </div>
      </div>

      <style>{`
        @keyframes bounce {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-10px); }
        }
      `}</style>
    </div>
  );
}
